<template>
  <div>
    <div>
      <b-card id="taskCard" no-body>
        <b-row align-h="end" class="checkbox_row">
          <b-col cols="6" md="2" sm="6" class="checkbox_col">
            <b-checkbox @change="displayOwnTask" class="">
              {{ $t("DISPLAYOWNTASK") }}
            </b-checkbox>
          </b-col>
          <b-col cols="6" md="2" sm="6" class="checkbox_col">
            <b-checkbox @change="displayProcessed" class="" checked="true">
              {{ $t("DISPLAYPROCESSED") }}
            </b-checkbox>
          </b-col>
        </b-row>
        <b-tabs v-model="tabIndex" class="tabs">
          <b-tab
            v-if="getContentLevelAccess('task-detail-cost-reduction-crm-11')"
            @click="tabTouchProcess"
            :title="$t('COSTREDUCTIONTAB')"
          >
            <b-card no-body>
              <div>
                <b-table
                  ref="taskListTable"
                  :select-mode="selectModeTable"
                  :fields="fields"
                  :busy="isBusy"
                  :items="tableData"
                  :per-page="limit"
                  :total="total"
                  :startPage="start"
                  @change-page="pageChange"
                  :bordered="false"
                  :borderless="false"
                  :outlined="false"
                  head-variant="light"
                  small
                  fixed
                  responsive
                >
                  <template v-slot:cell(checkState)="item">
                    <b-form-checkbox
                      v-if="getCheckBoxControl(item.item)"
                      :id="'checkbox-' + item.index"
                      v-model="item.item.checkState"
                      :name="'checkbox-' + item.index"
                      @change="costToggleSelectAll(item)"
                    ></b-form-checkbox>
                    <b-form-checkbox v-else disabled></b-form-checkbox>
                  </template>
                  <template v-slot:cell(icon)="item">
                    <template>
                      <b-button
                        :class="{
                          redBtn: item.value == '01',
                          skyBlueBtn: item.value == '02',
                          grayBtn: item.value == '03',
                        }"
                        >{{ taskStatusKbn[item.value] }}
                      </b-button>
                    </template>
                  </template>
                  <template v-slot:cell(task)="item">
                    <b-link @click="seeTaskDetails(item)"
                      ><u>{{ item.value }}</u></b-link
                    >
                  </template>
                  <template v-slot:cell(expenseItem)="item">{{
                    item.value
                  }}</template>
                  <template v-slot:cell(companyName)="item"
                    >{{ item.value }}
                  </template>
                  <template v-slot:cell(industry)="item">{{
                    item.value
                  }}</template>
                  <template v-slot:cell(prefectures)="item">{{
                    item.value
                  }}</template>
                  <template v-slot:cell(requestTime)="item">{{
                    item.value
                  }}</template>
                  <template v-slot:cell(processer)="item">{{
                    item.value
                  }}</template>
                  <template v-slot:cell(processingTime)="item">{{
                    item.value
                  }}</template>
                  <template v-slot:table-busy>
                    <div class="text-center text-danger my-2">
                      <b-spinner class="align-middle"></b-spinner>
                    </div>
                  </template>
                </b-table>
              </div>
            </b-card>
          </b-tab>
          <b-tab
            v-if="getContentLevelAccess('task-process-subsidy-crm-09')"
            @click="subsidyTabTouchProcess"
            :title="$t('SUBSIDYTAB')"
          >
            <b-card no-body>
              <div>
                <b-table
                  ref="taskListTable"
                  :select-mode="selectModeTable"
                  :fields="subsidyFields"
                  :busy="isBusy"
                  :items="subsidyData"
                  :per-page="limit"
                  :total="total"
                  :startPage="start"
                  @change-page="pageChange"
                  :bordered="false"
                  :borderless="false"
                  :outlined="false"
                  head-variant="light"
                  small
                  fixed
                  responsive
                >
                  <template v-slot:cell(checkState)="item">
                    <b-form-checkbox
                      v-if="getCheckBoxControl(item.item)"
                      :id="'subsidycheckbox-' + item.index"
                      v-model="item.item.checkState"
                      :name="'subsidycheckbox-' + item.index"
                      @change="subsidyToggleSelectAll(item)"
                    ></b-form-checkbox>
                    <b-form-checkbox v-else disabled></b-form-checkbox>
                  </template>
                  <template v-slot:cell(icon)="item">
                    <template>
                      <b-button
                        :class="{
                          redBtn: item.value == '01',
                          skyBlueBtn: item.value == '02',
                          grayBtn: item.value == '03',
                        }"
                        >{{ taskStatusKbn[item.value] }}
                      </b-button>
                    </template>
                  </template>
                  <template v-slot:cell(task)="item">
                    <b-link @click="seeSubsidyDetails(item)"
                      ><u>{{ item.value }}</u></b-link
                    >
                  </template>
                  <template v-slot:cell(subsidyType)="item">{{
                    item.value
                  }}</template>
                  <template v-slot:cell(companyName)="item"
                    >{{ item.value }}
                  </template>
                  <template v-slot:cell(industry)="item">{{
                    item.value
                  }}</template>
                  <template v-slot:cell(prefectures)="item">{{
                    item.value
                  }}</template>
                  <template v-slot:cell(requestTime)="item">{{
                    item.value
                  }}</template>
                  <template v-slot:cell(processer)="item">{{
                    item.value
                  }}</template>
                  <template v-slot:cell(processingTime)="item">{{
                    item.value
                  }}</template>
                  <template v-slot:table-busy>
                    <div class="text-center text-danger my-2">
                      <b-spinner class="align-middle"></b-spinner>
                    </div>
                  </template>
                </b-table>
              </div>
            </b-card>
          </b-tab>
          <!-- disable the Tab Becauase of Phase2 -->
          <b-tab
            v-if="getContentLevelAccess('task-detail-input-agency-crm-13')"
            @click="agencyTabTouchProcess"
            :title="$t('INPUTAGENCYTAB')"
            :disabled="true"
          >
            <div>
              <b-table
                ref="selectableTable"
                :select-mode="selectModeTable"
                :busy="isBusy"
                :fields="agencyfields"
                :items="inputAgencyTableData"
                :per-page="limit"
                :total="total"
                :startPage="start"
                @change-page="pageChange"
                :bordered="false"
                :borderless="false"
                :outlined="false"
                head-variant="light"
                small
                fixed
                responsive
              >
                <template v-slot:cell(checkState)="item">
                  <b-form-checkbox
                    v-if="getCheckBoxControl(item.item)"
                    :id="'agencyCheckbox-' + item.index"
                    v-model="item.item.checkState"
                    :name="'agencyCheckbox-' + item.index"
                    @change="agencyToggleSelectAll(item)"
                  ></b-form-checkbox>
                  <b-form-checkbox v-else disabled></b-form-checkbox>
                </template>
                <template v-slot:cell(icon)="item">
                  <template>
                    <b-button
                      :class="{
                        redBtn: item.value == '01',
                        skyBlueBtn: item.value == '02',
                        grayBtn: item.value == '03',
                      }"
                      >{{ taskStatusKbn[item.value] }}
                    </b-button>
                  </template>
                </template>
                <template v-slot:cell(task)="item">
                  <b-link @click="seeAgencyTaskDetails(item)"
                    ><u>{{ item.value }}</u></b-link
                  >
                </template>
                <template v-slot:cell(companyName)="item">{{
                  item.value
                }}</template>
                <template v-slot:cell(industry)="item">{{
                  item.value
                }}</template>
                <template v-slot:cell(prefectures)="item">{{
                  item.value
                }}</template>
                <template v-slot:cell(requestTime)="item">{{
                  item.value
                }}</template>
                <template v-slot:cell(processer)="item">{{
                  item.value
                }}</template>
                <template v-slot:cell(processingTime)="item">{{
                  item.value
                }}</template>
                <template v-slot:table-busy>
                  <div class="text-center text-danger my-2">
                    <b-spinner class="align-middle"></b-spinner>
                  </div>
                </template>
              </b-table>
            </div>
          </b-tab>
        </b-tabs>
      </b-card>
    </div>
    <b-row>
      <b-col cols="12" xl="4">
        <b-button
          @click="updateTaskData"
          :disabled="!checkedItems.length > 0"
          variant="primary"
          >{{ $t("CHECKEDTASKBATCH") }}</b-button
        >
      </b-col>
      <b-col cols="12" xl="5">
        <b-pagination
          v-model="currentPage"
          :total-rows="totalRows"
          :per-page="limit"
          class="paginateProcess"
          align="center"
          size="sm"
        >
          <template v-slot:prev-text>{{ $t("PREVIOUS") }}</template>
          <template v-slot:next-text>{{ $t("NEXT") }}</template>
          <template v-slot:page="{ page, active }">
            <strong v-if="active">{{ page }}</strong>
            <i v-else>{{ page }}</i>
          </template>
        </b-pagination>
      </b-col>
      <b-col cols="12" xl="3">
        <div class="countList">
          <label>{{ $t("MEANS") }}</label
          >&nbsp;&nbsp;
          <template v-for="(page, index) in pageOptions">
            <b-link :key="index" v-if="limit == page" href="javascript:;">
              {{ page }}
              <span v-if="index != pageOptions.length - 1">&nbsp;|&nbsp;</span>
            </b-link>
            <b-link
              :key="index"
              @click="perPageChange(page)"
              v-else
              href="javascript:;"
            >
              <u>{{ page }}</u>
              <span v-if="index != pageOptions.length - 1">&nbsp;|&nbsp;</span>
            </b-link>
          </template>
        </div>
      </b-col> </b-row
    ><br />
  </div>
</template>
<script>
import { ApiServicesFactory } from "@/shared/apiServiceFactory";
const BasicInfoApis = ApiServicesFactory.get("basic");
import { getDateHourMinute } from "@/shared/utils";
import { Settings } from "luxon";
Settings.defaultLocale = "ja";
import { endPoint } from "@/shared/endPoint";

export default {
  name: "Index",
  data() {
    return {
      tabIndex: 0,
      perPage: 2,
      liveTab: 0,
      selectModeTable: "single",
      selectedRow: [],
      taskKbn: [],
      tableData: [],
      subsidyType: {
        "001": this.$t("SUBSIDYTYPE.LABOURRELATED"),
        "002": this.$t("SUBSIDYTYPE.TRAININGRELATED"),
        "003": this.$t("SUBSIDYTYPE.CAPITALRELATED"),
        "004": this.$t("SUBSIDYTYPE.OTHER"),
      },
      statusKbn: {
        "01": this.$t("SUBSIDY.ASSESSMENTREQUEST"),
        "02": this.$t("SUBSIDY.UNDERASSESSMENT"),
        "03": this.$t("SUBSIDY.APPLYING"),
        "04": this.$t("SUBSIDY.APPLIED"),
      },
      taskStatusKbn: {
        "01": this.$t("UNPROCESSED"),
        "02": this.$t("PROCESSING"),
        "03": this.$t("PROCESSED"),
      },
      checkedItems: [],
      inputAgencyTableData: [],
      subsidyData: [],
      isBusy: false,
      currentTabIndex: 0,
      selectedTask: {},
      displayCheck: false,
      processedCheck: true,
      total: 0,
      limit: 50,
      start: 0,
      currentPage: 1,
      pageOptions: [50, 100, 200],
    };
  },
  computed: {
    totalRows() {
      if (this.total) {
        return this.total;
      }
      return this.getRowCount();
    },
    items() {
      return this.tableData;
    },
    subsidyFields() {
      return [
        {
          key: "checkState",
          label: "",
          class: "text-center check-normal-width",
        },
        {
          key: "icon",
          label: this.$t("TASK.TASKSTATUS"),
          class: "text-center title-normal-width",
        },
        {
          key: "task",
          label: this.$t("TASK.TASK"),
          class: "text-center normal-width",
        },
        {
          key: "subsidyType",
          label: this.$t("TASK.SUBSIDYTYPE"),
          class: "text-center normal-width",
        },
        {
          key: "companyName",
          label: this.$t("TASK.COMPANYNAME"),
          class: "text-center date-normal-width",
        },
        {
          key: "industry",
          label: this.$t("TASK.INDUSTRY"),
          class: "text-center title-normal-width",
        },
        {
          key: "prefectures",
          label: this.$t("TASK.PREFECTURES"),
          class: "text-center normal-width",
        },
        {
          key: "requestTime",
          label: this.$t("TASK.REQUESTDATETIME"),
          class: "text-center date-normal-width",
        },
        {
          key: "processer",
          label: this.$t("TASK.PERSONINCHARGE"),
          class: "text-center normal-width",
        },
        {
          key: "processingTime",
          label: this.$t("TASK.PROCESSINGDATETIME"),
          class: "text-center date-normal-width",
        },
      ];
    },
    agencyfields() {
      return [
        {
          key: "checkState",
          label: "",
          class: "text-center check-normal-width",
        },
        {
          key: "icon",
          label: this.$t("TASK.TASKSTATUS"),
          class: "text-center title-normal-width",
        },
        {
          key: "task",
          label: this.$t("TASK.TASK"),
          class: "text-center normal-width",
        },
        {
          key: "companyName",
          label: this.$t("TASK.COMPANYNAME"),
          class: "text-center date-normal-width",
        },
        {
          key: "industry",
          label: this.$t("TASK.INDUSTRY"),
          class: "text-center title-normal-width",
        },
        {
          key: "prefectures",
          label: this.$t("TASK.PREFECTURES"),
          class: "text-center normal-width",
        },
        {
          key: "requestTime",
          label: this.$t("TASK.REQUESTDATETIME"),
          class: "text-center date-normal-width",
        },
        {
          key: "processer",
          label: this.$t("TASK.PERSONINCHARGE"),
          class: "text-center normal-width",
        },
        {
          key: "processingTime",
          label: this.$t("TASK.PROCESSINGDATETIME"),
          class: "text-center date-normal-width",
        },
      ];
    },
    fields() {
      return [
        {
          key: "checkState",
          label: "",
          class: "text-center check-normal-width",
        },
        {
          key: "icon",
          label: this.$t("TASK.TASKSTATUS"),
          class: "text-center title-normal-width",
        },
        {
          key: "task",
          label: this.$t("TASK.TASK"),
          class: "text-center normal-width",
        },
        {
          key: "expenseItem",
          label: this.$t("TASK.EXPENSEITEM"),
          class: "text-center normal-width",
        },
        {
          key: "companyName",
          label: this.$t("TASK.COMPANYNAME"),
          class: "text-center date-normal-width",
        },
        {
          key: "industry",
          label: this.$t("TASK.INDUSTRY"),
          class: "text-center title-normal-width",
        },
        {
          key: "prefectures",
          label: this.$t("TASK.PREFECTURES"),
          class: "text-center normal-width",
        },
        {
          key: "requestTime",
          label: this.$t("TASK.REQUESTDATETIME"),
          class: "text-center date-normal-width",
        },
        {
          key: "processer",
          label: this.$t("TASK.PERSONINCHARGE"),
          class: "text-center normal-width",
        },
        {
          key: "processingTime",
          label: this.$t("TASK.PROCESSINGDATETIME"),
          class: "text-center date-normal-width",
        },
      ];
    },
  },
  watch: {
    currentPage() {
      this.pageChange(this.currentPage);
    },
    startPage() {
      this.currentPage = this.startPage + 1;
    },
  },
  validations: {},
  mounted() {
    let wiz_flag = localStorage.getItem("wiz_auth");
    let admin_flg = localStorage.getItem("admin_auth");
    let supplier_flag = localStorage.getItem("supplier_auth");
    if (
      wiz_flag == "true" &&
      supplier_flag == "false" &&
      admin_flg == "false"
    ) {
      this.currentTabIndex = 1;
      this.liveTab = localStorage.getItem("liveTab");
      if (this.liveTab) {
        this.tabIndex = parseInt(this.liveTab) - 1;
        this.liveTabSwitch();
      } else {
        this.getSubsidyData();
      }
    } else {
      this.liveTab = localStorage.getItem("liveTab");
      if (this.liveTab) {
        this.tabIndex = parseInt(this.liveTab);
        this.liveTabSwitch();
      } else {
        this.getTaskListData();
      }
    }
  },
  created() {},
  methods: {
    liveTabSwitch() {
      switch (this.liveTab) {
        case "0":
          return this.getTaskListData();
        case "1":
          return this.getSubsidyData();
        case "2":
          return this.getAgencyTaskList();
      }
    },
    getContentLevelAccess(accessID) {
      let checkData = JSON.parse(localStorage.getItem("dummyData"));
      const foundAccess = checkData ? checkData.hasOwnProperty(accessID) : true;
      //console.log("GGGGGGGGG", foundAccess);
      if (foundAccess) {
        return false;
      } else {
        return true;
      }
    },
    getRowCount() {
      return this.items.length;
    },
    pageChange(page) {
      this.start = page - 1;
      this.liveTab = localStorage.getItem("liveTab");
      let caseValue = this.liveTab
        ? parseInt(this.liveTab)
        : this.currentTabIndex;
      switch (caseValue) {
        case 0:
          this.getTaskListData();
          break;
        case 1:
          this.getSubsidyData();
          break;
        case 2:
          this.getAgencyTaskList();
          break;

        default:
          break;
      }
    },
    perPageChange(page) {
      this.limit = page;
      this.liveTab = localStorage.getItem("liveTab");
      let caseValue = this.liveTab
        ? parseInt(this.liveTab)
        : this.currentTabIndex;
      console.log("PPPPPPP", this.limit, page, caseValue);
      switch (caseValue) {
        case 0:
          this.getTaskListData();
          break;
        case 1:
          this.getSubsidyData();
          break;
        case 2:
          this.getAgencyTaskList();
          break;

        default:
          break;
      }
    },
    getCheckBoxControl(item) {
      let personID = localStorage.getItem("personID");
      let admin_flg = localStorage.getItem("admin_auth");
      return (admin_flg == "true" || personID == item.task_person_id) &&
        item.icon != "03"
        ? true
        : false;
    },
    subsidyToggleSelectAll(item) {
      item.item.checkState = !item.item.checkState;
      this.checkedItems = this.subsidyData.filter((checkedItem) => {
        if (checkedItem.checkState == true) {
          return checkedItem;
        }
      });
    },
    agencyToggleSelectAll(item) {
      item.item.checkState = !item.item.checkState;
      this.checkedItems = this.inputAgencyTableData.filter((checkedItem) => {
        if (checkedItem.checkState == true) {
          return checkedItem;
        }
      });
    },
    costToggleSelectAll(item) {
      item.item.checkState = !item.item.checkState;
      this.checkedItems = this.tableData.filter((checkedItem) => {
        if (checkedItem.checkState == true) {
          return checkedItem;
        }
      });
    },
    async updateTaskData() {
      let compileData = this.checkedItems;
      let updatedCompiledArray = [];
      compileData.forEach((item) => {
        let iterateItem = {
          task_kbn: "03",
          task_body_no: item.task_body_no,
          task_type: item.task_type,
          task_dt: getDateHourMinute(new Date()),
          del_flg: false,
          update_id: localStorage.getItem("personID"),
          update_dt: getDateHourMinute(new Date()),
        };
        let compiledArray = {
          data: [iterateItem],
          conditions: [
            {
              key: "task_no",
              value: item.task_no,
            },
          ],
        };
        updatedCompiledArray.push(compiledArray);
      });
      const { data } = await BasicInfoApis.postAPI(
        endPoint.TASKUPDATETASK,
        updatedCompiledArray
      );
      if (data.success) {
        this.refreshAfterUpdate();
      }
    },
    refreshAfterUpdate() {
      console.log("OOOOOOOOOO", this.currentTabIndex);
      this.liveTab = localStorage.getItem("liveTab");
      let caseValue = this.liveTab
        ? parseInt(this.liveTab)
        : this.currentTabIndex;
      switch (caseValue) {
        case 0:
          this.getTaskListData();
          break;
        case 1:
          this.getSubsidyData();
          break;
        case 2:
          this.getAgencyTaskList();
          break;

        default:
          break;
      }
    },
    displayOwnTask() {
      if (this.displayCheck) {
        this.displayCheck = false;
      } else {
        this.displayCheck = true;
      }
      this.liveTab = localStorage.getItem("liveTab");
      let caseValue = this.liveTab
        ? parseInt(this.liveTab)
        : this.currentTabIndex;
      console.log("OOOOOOOOOO", this.currentTabIndex);
      switch (caseValue) {
        case 0:
          this.getTaskListData();
          break;
        case 1:
          this.getSubsidyData();
          break;
        case 2:
          this.getAgencyTaskList();
          break;

        default:
          break;
      }
    },
    displayProcessed() {
      if (this.processedCheck) {
        this.processedCheck = false;
      } else {
        this.processedCheck = true;
      }
      this.liveTab = localStorage.getItem("liveTab");
      let caseValue = this.liveTab
        ? parseInt(this.liveTab)
        : this.currentTabIndex;
      switch (caseValue) {
        case 0:
          this.getTaskListData();
          break;
        case 1:
          this.getSubsidyData();
          break;
        case 2:
          this.getAgencyTaskList();
          break;

        default:
          this.getTaskListData();
          break;
      }
      console.log("Processed", this.currentTabIndex, caseValue);
    },

    tabTouchProcess() {
      this.currentPage = 1;
      this.limit = 50;
      this.total = 0;
      this.start = 0;
      this.currentTabIndex = 0;
      localStorage.setItem("liveTab", this.currentTabIndex);
      this.getTaskListData();
    },
    subsidyTabTouchProcess() {
      this.currentPage = 1;
      this.limit = 50;
      this.total = 0;
      this.start = 0;
      this.currentTabIndex = 1;
      localStorage.setItem("liveTab", this.currentTabIndex);
      this.getSubsidyData();
    },
    agencyTabTouchProcess() {
      this.currentPage = 1;
      this.limit = 50;
      this.total = 0;
      this.start = 0;
      this.currentTabIndex = 2;
      localStorage.setItem("liveTab", this.currentTabIndex);
      this.getAgencyTaskList();
    },

    async getTaskListData() {
      this.checkedItems = [];
      let personID = "";
      this.isBusy = true;
      if (this.displayCheck) {
        personID = localStorage.getItem("personID");
      }
      const { data } = await BasicInfoApis.postAPI(endPoint.TASKTASKPROCESS, {
        task_own: this.displayCheck,
        task_status: this.processedCheck,
        task_person_id: personID,
        limit: this.limit,
        page: this.currentPage,
      });
      if (data.success) {
        let responseData = data.data;
        this.total = 0;
        let rowData = [];
        if (responseData.data.length) {
          this.total = responseData.total;
          responseData.data.forEach((item) => {
            let iterateTask = {
              checkState: false,
              icon: item.task_kbn,
              task_no: item.task_no,
              task_body_no: item.task_body_no ? item.task_body_no : "",
              task_type: item.task_type,
              task:
                item.task_type == "001"
                  ? this.$t("REQUESTQUOTATIONTASK")
                  : this.$t("CONTRACTTASK"),
              task_person_id: item.task_person_id,
              client_no: item.client_no ? item.client_no : "",
              expenseItem: item.expense_item_nm ? item.expense_item_nm : "",
              companyName: item.client_name ? item.client_name : "",
              industry: item.industry_nm ? item.industry_nm : "",
              reduction_proposal_no: item.reduction_proposal_no
                ? item.reduction_proposal_no
                : "",
              prefectures: item.prefectures ? item.prefectures : "",
              requestTime: this.getDateFormat(item.create_dt),
              processer: item.person_name
                ? item.person_name
                : this.$t("PROCESSERNAMENOTSET"),
              processingTime: this.getDateFormat(item.task_dt),
            };
            rowData.push(iterateTask);
          });
          this.isBusy = false;
          this.tableData = rowData;
        } else {
          this.isBusy = false;
          this.tableData = [];
        }
      } else {
        this.isBusy = false;
        this.tableData = [];
      }
    },
    getDateFormat(date) {
      if (!date) {
        return "";
      }
      return (
        date.slice(0, 4) +
        "/" +
        date.slice(5, 7) +
        "/" +
        date.slice(8, 10) +
        " " +
        date.slice(11, 16)
      );
    },
    async getSubsidyData() {
      this.checkedItems = [];
      let personID = "";
      this.isBusy = true;
      if (this.displayCheck) {
        //personID = 3;
        personID = localStorage.getItem("personID");
      }
      const { data } = await BasicInfoApis.postAPI(
        endPoint.TASKREADTASKPROCESSSUBSIDYASSESSMENT,
        {
          task_own: this.displayCheck,
          task_status: this.processedCheck,
          task_person_id: personID,
          limit: this.limit,
          page: this.currentPage,
        }
      );
      if (data.success) {
        let responseData = data.data;
        let rowData = [];
        this.total = 0;
        if (responseData.data.length > 0) {
          this.total = responseData.total;
          responseData.data.forEach((item) => {
            let iterateTask = {
              checkState: false,
              icon: item.task_kbn,
              task_no: item.task_no,
              task_body_no: item.task_body_no ? item.task_body_no : "",
              task_type: item.task_type,
              task: item.task_type == "003" ? this.$t("SUBSIDYTASK") : "",
              task_person_id: item.task_person_id,
              client_no: item.client_no ? item.client_no : "",
              subsidyType: item.subsidy_type
                ? this.subsidyType[item.subsidy_type]
                : "",
              companyName: item.client_name ? item.client_name : "",
              industry: item.industry_nm ? item.industry_nm : "",
              prefectures: item.prefectures ? item.prefectures : "",
              requestTime: this.getDateFormat(item.assessment_dt),
              processer: item.person_name
                ? item.person_name
                : this.$t("PROCESSERNAMENOTSET"),
              processingTime: this.getDateFormat(item.task_dt),
            };
            rowData.push(iterateTask);
          });
          this.isBusy = false;
          this.subsidyData = rowData;
        } else {
          this.isBusy = false;
          this.subsidyData = [];
        }
      } else {
        this.isBusy = false;
        this.subsidyData = [];
      }
    },
    async getAgencyTaskList() {
      this.checkedItems = [];
      let personID = "";
      this.isBusy = true;
      if (this.displayCheck) {
        //personID = 3;
        personID = localStorage.getItem("personID");
      }
      const { data } = await BasicInfoApis.postAPI(
        endPoint.TASKREADTASKPROCESSINGAGENT,
        {
          task_own: this.displayCheck,
          task_status: this.processedCheck,
          task_person_id: personID,
          limit: this.limit,
          page: this.currentPage,
        }
      );
      if (data.success) {
        let responseData = data.data;
        this.total = 0;
        let rowData = [];
        if (responseData.data.length > 0) {
          this.total = responseData.total;
          responseData.data.forEach((item) => {
            let iterateTask = {
              checkState: false,
              icon: item.task_kbn,
              task_no: item.task_no,
              task_body_no: item.task_body_no ? item.task_body_no : "",
              task_type: item.task_type,
              task: item.task_type == "004" ? this.$t("AGENCYTASK") : "",
              task_person_id: item.task_person_id,
              client_no: item.client_no ? item.client_no : "",
              companyName: item.client_name ? item.client_name : "",
              industry: item.industry_nm ? item.industry_nm : "",
              prefectures: item.prefectures ? item.prefectures : "",
              requestTime: this.getDateFormat(item.create_dt),
              processer: item.person_name
                ? item.person_name
                : this.$t("PROCESSERNAMENOTSET"),
              processingTime: this.getDateFormat(item.task_dt),
            };
            rowData.push(iterateTask);
          });
          this.isBusy = false;
          this.inputAgencyTableData = rowData;
        } else {
          this.isBusy = false;
          this.inputAgencyTableData = [];
        }
      } else {
        this.isBusy = false;
        this.inputAgencyTableData = [];
      }
      this.inputAgencyTableData.forEach((element, index) => {
        if (index === this.inputAgencyTableData.length - 1) {
          this.inputAgencyTableData[index].last_element = true;
          return;
        }
      });
    },
    seeTaskDetails(item) {
      this.selectedTask = item;
      console.log("KKKKK", item);
      this.$store.dispatch("updateTaskData", {
        task_no: item.item.task_no,
        task_type: item.item.task_type,
        client_no: item.item.client_no,
      });
      this.$router.push({ name: "task_details" });
    },
    seeSubsidyDetails(item) {
      this.selectedTask = item;
      this.$store.dispatch("updateTaskData", {
        task_no: item.item.task_no,
        task_type: item.item.task_type,
        client_no: item.item.client_no,
      });
      this.$router.push({ name: "task_subsidy_details" });
    },
    seeAgencyTaskDetails(item) {
      this.selectedTask = item;
      this.$store.dispatch("updateTaskData", {
        task_no: item.item.task_no,
        task_type: item.item.task_type,
        client_no: item.item.client_no,
      });
      this.$router.push({ name: "task_agency_details" });
    },
  },
};
</script>
